import React from "react";
import save from "../images/info-save.png"
import {Link} from 'gatsby';

const InfoBox = ({ info , title , text }) => {



    return (
       <>
           <div page={info} className="boxpage h-100 d-flex flex-column justify-content-center align-items-center">
               <div>
                   <img src={save} alt=""/>
               </div>
               <div className="title">
                   {title}
               </div>
               <div className="text">
                   {text}
               </div>
               <div className="box-btn">
                    <Link to={`/`} className="btn style-3 w100">
                        вернуться на главную
                    </Link>
               </div>
           </div>
       </>
    )

}
export default InfoBox